<template lang="pug">
  .page.publish-page
    v-header(v-bind:showBack="false" content="")
    .item
      .title 
      input(placeholder="10" v-model="title")
    .item
      .title 
      select(v-model="tab")
        option(v-for="(item, index) in categorys" v-bind:value="item.key") {{item.tab}}
    .item
      .title 
      textarea(placeholder="" v-model="content")
    .item: button(@click="publishTopic") 
</template>

<script>
  import mixin from '../mixins'
  import { mapActions } from 'vuex'

  export default {
    name: 'publish',
    data(){
      return {
        categorys: [{
          tab: '',
          key: 'share'
        }, {
          tab: '',
          key: 'ask'
        }, {
          tab: '',
          key: 'job'
        }],
        tab: 'share',
        title: '',
        content: '',
      }
    },
    methods: {
      ...mapActions(['createTopic']),
      publishTopic(){
        if(!this.title.trim() || this.title && this.title.trim().length < 10) return this.showToast({ msg: '10' })
          
        if(!this.content) return this.showToast({ msg: '' })
   
        this.createTopic({
          accesstoken: this.user.accesstoken,
          title: this.title,
          tab: this.tab,
          content: this.content
        }).then(res => {
          this.$router.push('/index')
        })
      }
    }
  }
</script>

<style lang="stylus">
  @import './Publish.stylus'
</style>


